export default {
	data() {
		return {
			title:''
		}
		
	},
	onLoad:function(e){//接收index传递过来的参数
		console.log(e);
	
		// reu
		uni.request({//与api交互
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' +e.newsid,//动态参数就是 详情页传回来的参数
			method: 'GET',//接收到数据
			data: {},
			success: res => {//接口调用成功的回调函数
				console.log(res);
				this.title = res.data.title;
				this.strings = res.data.content;
			},
			fail: () => {},
			complete: () => {}
		});
	}
	// methods: {
		
	// }
}
